"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useToast } from "@/components/ui/use-toast"
import { useCart } from "@/contexts/cart-context"

interface Table {
  id: string
  number: number
  occupied: boolean
}

export default function ScanPage() {
  const router = useRouter()
  const { toast } = useToast()
  const { setTable } = useCart()
  const [tables, setTables] = useState<Table[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchTables = async () => {
      try {
        const response = await fetch("/api/tables")
        if (!response.ok) {
          throw new Error("Failed to fetch tables")
        }
        const data = await response.json()
        setTables(data)
      } catch (error) {
        console.error("Error fetching tables:", error)
        setError("Failed to load tables. Please try again later.")
      } finally {
        setLoading(false)
      }
    }

    fetchTables()
  }, [])

  const handleTableSelect = async (tableId: string, tableNumber: number) => {
    try {
      const response = await fetch("/api/tables", {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          id: tableId,
          occupied: true,
        }),
      })

      if (!response.ok) {
        throw new Error("Failed to update table status")
      }

      setTable(tableId)
      toast({
        title: "Table selected",
        description: `You are now seated at Table ${tableNumber}`,
      })
      router.push("/menu")
    } catch (error) {
      console.error("Error updating table:", error)
      toast({
        title: "Error",
        description: "Failed to select table. Please try again.",
        variant: "destructive",
      })
    }
  }

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="animate-pulse space-y-4">
          {[1, 2, 3].map((i) => (
            <Card key={i} className="p-6">
              <div className="h-4 bg-gray-200 rounded w-1/4 mb-4"></div>
              <div className="h-8 bg-gray-200 rounded w-24"></div>
            </Card>
          ))}
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Card className="p-6 text-center">
          <p className="text-red-500 mb-4">{error}</p>
          <Button onClick={() => window.location.reload()}>
            Try Again
          </Button>
        </Card>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Select Your Table</h1>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
        {tables.map((table) => (
          <Card key={table.id} className="p-6">
            <div className="text-center">
              <h2 className="text-2xl font-bold mb-4">Table {table.number}</h2>
              <Button
                className="w-full"
                disabled={table.occupied}
                onClick={() => handleTableSelect(table.id, table.number)}
              >
                {table.occupied ? "Occupied" : "Select Table"}
              </Button>
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}
